import React, { useState } from "react";
import styled from "styled-components";
import { Container, Row, Col, Form, Button } from "react-bootstrap";
import firebase from "../../firebase";
import ContactUs1 from "../../img/contactus1.svg";
import Dialog from "@material-ui/core/Dialog";
import {
  DialogContentText,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@material-ui/core";

const ContactUsSection1 = () => {
  const [values, setValues] = useState({
    name: "",
    email: "",
    contactNumber: "",
    message: "",
  });
  const [error, setErr] = useState({});
  const [display, setDisplay] = useState(false);
  const [open, setOpen] = useState(false);

  const handleSubmit = (e) => {
    e.preventDefault();
    const isValid = handleValidation();
    if (isValid) {
      const db = firebase.firestore();
      db.collection("contactus")
        .add({
          name: values.name,
          email: values.email,
          contactNumber: values.contactNumber,
          message: values.message,
        })
        .then(() => {
          setDisplay(true);
          setOpen(true);
        })
        .catch((error) => {
          alert(error.message);
        });
      setValues({ name: "", email: "", contactNumber: "", message: "" });
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setValues({ ...values, [name]: value });
  };

  const handleValidation = () => {
    let isValid = true;
    let nameErr = {};
    const regex1 = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;

    if (values.name.trim().length < 4) {
      nameErr.name = "Please enter your full name (first & last name).";
      isValid = false;
    }
    if (!regex1.test(values.email)) {
      nameErr.email = "Please enter a valid email";
      isValid = false;
    }

    if (values.contactNumber.trim().length <= 9) {
      nameErr.contactNumber = "Please enter a valid conatct number";
      isValid = false;
    }
    if (values.message.length < 10) {
      nameErr.message = "Please write something for us";
      isValid = false;
    }
    setErr(nameErr);
    return isValid;
  };

  const handleToClose = () => {
    setOpen(false);
  };

  return (
    <ContactS1>
      <Container>
        <Row id="first">
          <Col>
            <h1>Contact Us</h1>
            <h3>LET’S GET IN TOUCH</h3>
            <p>
              If you’re considering OvitLab or just want more information,simply
              fill out the form and we’ll be in touch.
            </p>
          </Col>
        </Row>
        <Row id="second">
          <Col md={6}>
            <Form noValidate onSubmit={handleSubmit}>
              <Form.Row>
                <Form.Group as={Col}>
                  <Form.Label> Name</Form.Label>
                  <Form.Control
                    type="text"
                    name="name"
                    placeholder="Name"
                    value={values.name}
                    onChange={handleChange}
                  />
                  {error.name && <p>{error.name}</p>}
                </Form.Group>
              </Form.Row>
              <Form.Row>
                <Form.Group as={Col}>
                  <Form.Label>Email Address</Form.Label>
                  <Form.Control
                    type="email"
                    name="email"
                    placeholder=" Email"
                    value={values.email}
                    onChange={handleChange}
                  />
                  {error.email && <p>{error.email}</p>}
                </Form.Group>
              </Form.Row>
              <Form.Row>
                <Form.Group as={Col}>
                  <Form.Label>Contact Number</Form.Label>
                  <Form.Control
                    type="text"
                    name="contactNumber"
                    placeholder=" Contact Number"
                    value={values.contactNumber}
                    onChange={handleChange}
                  />
                  {error.contactNumber && <p>{error.contactNumber}</p>}
                </Form.Group>
              </Form.Row>
              <Form.Row>
                <Form.Group as={Col}>
                  <Form.Label>Message</Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={3}
                    name="message"
                    placeholder="Message"
                    value={values.message}
                    onChange={handleChange}
                  />
                  {error.message && <p>{error.message}</p>}
                </Form.Group>
              </Form.Row>
              <p id="special">
                <Button variant="primary" size="sm" type="submit">
                  Submit
                </Button>
              </p>
            </Form>
            <div id="dis" className={`${display ? "visible" : "hidden"}`}>
              <Dialog open={open}>
                <DialogTitle>{" Your Message has been sent."}</DialogTitle>
                <DialogContent>
                  <DialogContentText>Thank You!</DialogContentText>
                </DialogContent>
                <DialogActions>
                  <Button onClick={handleToClose} color="primary" autoFocus>
                    Close
                  </Button>
                </DialogActions>
              </Dialog>
            </div>
          </Col>
          <Col md={6}>
            <img src={ContactUs1} alt="contactus1" />
          </Col>
        </Row>
      </Container>
    </ContactS1>
  );
};

const ContactS1 = styled.div`
  min-height: 60vh;
  padding-left: 0.5em;
  #first {
    padding: 10em 0em 1em 0em;
    h1 {
      padding: 0.3em 0em;
      font-size: 3.5em;
      font-weight: bold;
      color: #6c757d;
    }
    h3 {
      padding: 0.5em 0em;
      font-size: 2em;
      font-weight: bold;
      color: #6c757d;
    }
    p {
      padding: 0.3em 0em 1.5em 0em;
      font-size: 1.4em;
      display: block;
    }
    .Button-primary {
      color: #fff;
      background-color: #317cc1;
      border-color: #317cc1;
    }
  }

  #second {
    padding: 0em 1em 9em 0em;
    img {
      width: 100%;
      height: 100%;
      object-fit: fill;
    }
    Button {
      padding: 0.5em 1em;
    }
    p {
      padding: 0.5em 0em;
      font-size: 1.2em;
      color: red;
    }
    #special {
      padding: 0em;
    }

    .visible {
      opacity: 1;
      transition: all 1s ease-in-out;
    }
    .hidden {
      opacity: 0;
    }
  }

  @media only screen and (max-width: 767px) {
    min-height: 30vh;
    padding-left: 0.4em;
    #first {
      padding: 6em 0em 0em 0em;

      h3 {
        padding-bottom: 0.2em;
        font-size: 1.3em;
      }
      h1 {
        padding: 0.1em 0em;
        font-size: 2em;
      }
      p {
        font-size: 1em;
        padding-bottom: 0.3em;
      }
      Button {
        font-weight: 400;
        font-size: 0.9em;
      }
    }
    #second {
      padding: 0em 0em 3em 0em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 25vh;

    #first {
      padding-top: 8em;
      h1 {
        padding: 0.3em 0em;
      }
      h5 {
        padding: 0em;
      }
      p {
        padding-bottom: 0.2em;
      }
    }
    #second {
      padding: 0em 1em 3em 0em;
      img {
        object-fit: fill;
      }
      .form-control {
        font-size: 1.4em;
      }
      label {
        font-size: 1.4em;
      }
      .btn {
        font-size: 0.8em;
      }
    }
  }
`;
export default ContactUsSection1;
