import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Card } from "react-bootstrap";
import Service1 from "../../img/service1.jpg";

const ServiceSection2 = () => {
  return (
    <ServiceS2>
      <Container>
        <Row>
          <Col md={12}>
            <div>
              <h2>Custom Software Development</h2>
              <img src={Service1} alt="service1" />
              <p>
                To produce the best solution possible, we pay close attention to
                your product vision. We welcome your suggestions. To provide
                stylish and interaction design we focus on a custom approach. No
                matter how complex the technological aspect of the product, it
                will be easy and intuitive to navigate. We are capable of
                producing clickable prototypes to provide a comprehensive vision
                of the final result.
              </p>
              <p>
                In the process of working toward a result we follow Scrum
                methodology, assuring a development workflow that is
                resource-effective and time-sensitive. To be confident that
                every individual part complements the unified system and results
                in consistent, reliable product functionality, exhaustive
                testing is performed throughout the entire development
                process.With the product designed, developed, and tested, it is
                now ready for launch.
              </p>
            </div>
          </Col>
        </Row>
      </Container>
    </ServiceS2>
  );
};
const ServiceS2 = styled.div`
  min-height: 70vh;
  border-bottom: 1px solid;
  border-top: 1px solid;
  border-color: #c1c1c1;
  .row {
    padding: 4em 0em 5em 0em;
  }
  .col-md-12 {
    padding-top: 1em;
  }
  div {
    display: block;
  }
  img {
    float: right;
    object-fit: scale-down;
    width: 70%;
    height: 40%;
  }
  h2 {
    padding-bottom: 1em;
    font-size: 2.5em;
    font-weight: bold;
    color: #6c757d;
  }
  p {
    padding: 0.3em 0em;
    font-size: 1em;
  }

  @media only screen and (max-width: 767px) {
    min-height: 50vh;
    .row {
      padding: 2em 0.1em;
    }
    h2 {
      padding-bottom: 0.5em;
      font-size: 2em;
    }
    .col-md-12 {
      padding-top: 0.5em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 40vh;
  }
`;
export default ServiceSection2;
