import React from "react";
import styled from "styled-components";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col, Card } from "react-bootstrap";

const HomeSection4 = () => {
  return (
    <HomeS4>
      <Container fluid="md">
        <Row>
          <Col sm>
            <h1>How We Work</h1>
          </Col>
        </Row>
        <Row>
          <Col>
            <Card>
              <Card.Body>
                <Card.Title>01</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">
                  Meeting With Client
                </Card.Subtitle>
                <Card.Text>
                  Any project we work on starts with an initial meeting and
                  discussion with our customer.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col>
            <Card>
              <Card.Body>
                <Card.Title>02</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">
                  Concept Development
                </Card.Subtitle>
                <Card.Text>
                  After that, we start working on the concept of what our
                  clients need for their business.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col>
            <Card>
              <Card.Body>
                <Card.Title>03</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">
                  Analysis and Audit
                </Card.Subtitle>
                <Card.Text>
                  When we work on a project, our team also analyses a lot of
                  facts and information.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col>
            <Card>
              <Card.Body>
                <Card.Title>04</Card.Title>
                <Card.Subtitle className="mb-2 text-muted">
                  Project Presentation
                </Card.Subtitle>
                <Card.Text>
                  Finally, we present our work to the customer and make all
                  necessary changes.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </HomeS4>
  );
};
const HomeS4 = styled.div`
  background-color: #0065cb;
  color: white;
  min-height: 65vh;
  padding: 2em 4em;

  h1 {
    font-weight: 700;
    padding: 0.5em 0em 1em 0em;
    display: flex;
    justify-content: center;
  }
  .card {
    padding: 1em;
    font-size: 1.1em;
    height: 100%;
    width: 100%;
    border-radius: 0em;
  }
  .col {
    padding: 1em;
  }
  .card-title {
    color: #696969;
    padding: 1rem 0rem;
    font-size: 2em;
    text-align: left;
  }
  .card-text {
    color: #696969;
  }
  .mb-2,
  .text-muted,
  .card-subtitle h6 {
    color: #000000 !important;
    font-size: 1em;
    font-weight: 700;
    padding-bottom: 0.4em;
  }
  @media only screen and (max-width: 767px) {
    padding: 3em;
    min-height: 50vh;
    h1 {
      padding: 0.5em 0em;
      font-size: 1.9em;
    }
    .mb-2,
    .text-muted,
    .card-subtitle h6 {
      padding-bottom: 0em;
    }
    .card {
      padding: 0em;
      font-size: 1em;
    }
    .card-title {
      padding: 0.1rem 0rem;
      font-size: 1.5em;
    }
    .col {
      padding: 0.3em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    h1 {
      padding: 1em 0em;
    }
    min-height: 55vh;
  }
`;

export default HomeSection4;
