import React from "react";
import styled from "styled-components";
import { useHistory } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import { Button, Container, Row, Col } from "react-bootstrap";

const HomeSection5 = () => {
  const history = useHistory();
  return (
    <HomeS5>
      <Container>
        <Row>
          <Col sm>
            <h1>If you have an idea – no matter how big, we’re interested.</h1>
          </Col>
        </Row>
        <Row>
          <Col sm>
            <p>
              If you’ve been thinking about a software project, or you’d like to
              know more about how custom software can improve your business,
              we’d love to hear from you.
            </p>
          </Col>
        </Row>
        <Row>
          <Col sm>
            <Button
              variant="primary"
              size="lg"
              onClick={() => history.push("/contactus")}
            >
              LET'S TALK.
            </Button>
          </Col>
        </Row>
      </Container>
    </HomeS5>
  );
};
const HomeS5 = styled.div`
  min-height: 40vh;
  padding: 4em;
  .col-sm {
    display: flex;
    justify-content: center;
    padding: 0.6em;
    h1 {
      padding: 0.1em 1em;
      font-weight: 700;
      font-size: 2.1em;
      text-align: center;
    }
    p {
      padding: 0em 6em;
      text-align: center;
      line-height: 1.5;
      font-weight: 300;
      display: block;
      color: #787690;
    }
    Button {
      background-color: #0065cb;
      font-weight: 700;
      font-size: 1.1em;
      border-color: #317cc1;
    }
  }
  @media only screen and (max-width: 767px) {
    min-height: 30vh;
    padding: 1em 0.3em 2em 0.3em;
    .col-sm {
      padding: 0.3em;
      h1 {
        font-size: 1.4em;
      }
      p {
        padding: 0em 0.1em;
      }
      Button {
        font-weight: 400;
        font-size: 0.8em;
        border-color: #317cc1;
      }
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 25vh;
  }
`;

export default HomeSection5;
