import React from "react";
import styled from "styled-components";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Col } from "react-bootstrap";
import {
  FaWhatsapp,
  FaFacebook,
  FaTwitter,
  FaLinkedinIn,
} from "react-icons/fa";
import { GoMail } from "react-icons/go";
import { BiPhone, BiMap } from "react-icons/bi";

const FooterSection1 = () => {
  return (
    <Footer>
      <Container fluid>
        <Row id="first">
          <Col>
            <h1>OVIT LAB</h1>
            <ul id="social" className="list-unstyled">
              <li>
                <a href="">
                  <FaLinkedinIn />
                </a>
              </li>
              <li>
                <a href="">
                  <FaTwitter />
                </a>
              </li>
              <li>
                <a href="https://www.facebook.com/ovit.labcom.7">
                  <FaFacebook />
                </a>
              </li>
            </ul>
          </Col>
        </Row>
        <Row id="second">
          <div className="col-lg-3 ">
            <div id="ad1">
              <BiMap />
              <ul className="list-unstyled">
                <li>22 Telok Blangah Crescent</li>
                <li>090022,</li>
                <li>Singapore.</li>
              </ul>
            </div>
          </div>
          <div className="col-lg-3">
            <div id="ad2">
              <BiMap />
              <ul className="list-unstyled">
                <li>Nugegoda,</li>
                <li>10250,</li>
                <li>Sri Lanka.</li>
              </ul>
            </div>
          </div>
          <div className="col-lg-4">
            <ul id="detail" className="list-unstyled">
              <li>
                <p>
                  <GoMail />
                  ovitlabs@gmail.com
                </p>
              </li>
              <li>
                <p>
                  <BiPhone />
                  +6587484904
                </p>
              </li>
              <li>
                <p>
                  <FaWhatsapp />
                  +6587484904
                </p>
              </li>
            </ul>
          </div>
          <div className=" col-md-auto">
            <ul id="links" className="list-unstyled">
              <li>
                <a href="/">Home</a>
              </li>
              <li>
                <a href="/services">Services</a>
              </li>
              <li>
                <a href="/aboutus">About Us</a>
              </li>
              <li>
                <a href="/blogs">Blogs</a>
              </li>
              <li>
                <a href="/careers">Careers</a>
              </li>
              <li>
                <a href="/contactus">Contact Us</a>
              </li>
              <li>
                <a href="/privacypolicy">Privacy Policy</a>
              </li>
            </ul>
          </div>
        </Row>
      </Container>
    </Footer>
  );
};

const Footer = styled.div`
  min-height: 10vh;
  background-color: #020e26;
  padding: 1em 6em;
  color: #f8f8ff;
  #first {
    padding: 1.5em 0em 2em 0em;
  }
  #second {
    padding-top: 2em;
  }
  .row {
    h1 {
      display: flex;
      justify-content: center;
    }
    #social {
      display: flex;
      justify-content: center;
      li {
        padding: 0.6em;
      }
      svg {
        font-size: 1.8em;
        color: white;
      }
    }
  }
  .col-lg-3 {
    #ad1,
    #ad2 {
      display: flex;
      justify-content: left;
      svg {
        font-size: 1.7em;
      }
      li {
        padding-bottom: 0.5em;
      }
    }
  }
  .col-lg-4 {
    #detail {
      li {
        padding-bottom: 0.2em;

        display: flex;
      }
      svg {
        font-size: 1.7em;
        vertical-align: top;
        padding-right: 0.14em;
      }
      p {
        display: flex;
      }
    }
  }
  .col-md-auto {
    #links {
      a {
        color: #f8f8ff;
      }
      a:hover {
        color: #0065cb;
        text-decoration: none;
      }
      li {
        padding-bottom: 0.3em;
      }
    }
  }
  @media only screen and (max-width: 768px) {
    min-height: 9vh;
    padding: 1em 1em;
    #first {
      padding: 0.7em 0.5em;
    }
    #second {
      padding-top: 0em;
    }
    .col-lg-4 {
      #detail {
        padding-left: 0.4em;
        li {
          padding-bottom: 0em;
        }
        svg {
          font-size: 1.7em;
        }
      }
    }
    .col-md-auto {
      #links {
        padding-left: 0.6em;
      }
    }
    .row {
      #social {
        display: flex;
        justify-content: center;
        li {
          padding: 0.3em;
        }
        svg {
          font-size: 1.5em;
        }
      }
    }
  }
`;
export default FooterSection1;
