import React from "react";
import styled from "styled-components";
import { Container, Row, Col } from "react-bootstrap";

const PrivacySection1 = () => {
  return (
    <PrivacyS1>
      <Container>
        <Row>
          <Col>
            <h2>Privacy Statement</h2>
            <p>
              This Privacy policy enunciates the privacy and data protection
              principles followed by Ovit Lab with regards to collection, use,
              sharing and processing of personal information and personal data
              obtained in connection with the use of this website. This Privacy
              policy applies to processing of personal information or personal
              data of visitors and existing and prospective users of Ovit Lab.
            </p>
            <h2>What Information is Collected</h2>
            <p>
              We may collect, use, store and transfer different kinds of
              personal data about you which we have grouped together follows
              Identity Data includes first name, last name, username or similar
              identifier and title. Contact Data includes email address and
              telephone numbers. Usage Data includes information about how you
              use our Site, products and services.
            </p>
            <h2>How We Use Information</h2>
            <p>
              Ovit Lab may use your information for its own marketing purposes.
              This may include providing you with information targeted to your
              interests. Ovit Lab does not sell any personally identifiable
              information.
            </p>
            <h2>Data Security</h2>
            <p>
              We have put in place appropriate security measures to prevent your
              personal data from being accidentally lost, used or accessed in an
              unauthorised way, altered or disclosed. We have put in place
              procedures to deal with any suspected personal data breach and
              will notify you and any applicable regulator of a breach where we
              are legally required to do so. We take precautions to protect your
              information. When you submit sensitive information via the
              website, your information is protected both online and offline.
            </p>
            <h2>Changes to This Privacy Policy</h2>
            <p>
              We may update our Privacy Policy from time to time. Thus, you are
              advised to review this page periodically for any changes. We will
              notify you of any changes by posting the new Privacy Policy on
              this page.This policy is effective as of 2020-07-24.
            </p>
            <h2>Contact Us</h2>
            <p>
              If you have any questions or suggestions about our Privacy Policy,
              do not hesitate to contact us at ovitlabs@gmail.com.
            </p>
          </Col>
        </Row>
      </Container>
    </PrivacyS1>
  );
};
const PrivacyS1 = styled.div`
  min-height: 70vh;
  padding: 10em 1em;
  h2 {
    padding-bottom: 0.2em;
    color: #6c757d;
    font-weight: bold;
  }
  p {
    padding-bottom: 2em;
    font-size: 1.2em;
  }
  @media only screen and (max-width: 767px) {
    min-height: 30vh;
    padding: 7em 1em 2em 1em;
    h2 {
      font-size: 1.5em;
      padding-bottom: 0.1em;
    }
    p {
      padding-bottom: 1em;
      font-size: 1em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 20vh;
    padding: 10em 1em 2em 1em;
  }
`;
export default PrivacySection1;
