import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Button } from "react-bootstrap";
import Blog3 from "../../img/blog3.jpg";

const BlogSection3 = () => {
  return (
    <BlogS3>
      <Container>
        <Row>
          <Col md={12}>
            <h1>How to choose a Custom Software Development Company</h1>
            <div>
              <img src={Blog3} alt="blog3image" />
            </div>
            <h3>
              Choosing the right software development partner is the key factor
              in successful outsourcing
            </h3>
            <p>
              How do you find the best outsourcing partner? Where to start and
              what your requirements should look like? There are a lot of
              aspects you should take into consideration during the process, the
              same way as they were when purchasing a final product. People tend
              to look for the lowest price and the best fit for their
              requirements.
            </p>
            <p>
              What they are forgetting about is that hiring a vendor is like
              hiring a team of engineers for your project. It should be as much
              thought through process as in the case of employing people.You
              would not take hire the first person whose resume lands on your
              lap. Therefore, you should not take the first outsourcing company
              you come across.
            </p>
            <p>
              At first, you might feel overwhelmed by the number of vendors to
              pick from. It seems like nowadays everybody seems to be into
              outsourcing in one way or another. Hiring a ready-made team is the
              best option from the business point of view.You do not have time
              or money to spend researching single developers and trying to
              create a team. Somebody who is been working together for some time
              in the same office will deliver the best, most cohesive work while
              following well-defined and established a set of rules and
              practices.
            </p>
          </Col>
        </Row>
      </Container>
    </BlogS3>
  );
};

const BlogS3 = styled.div`
  min-height: 70vh;
  .row {
    padding: 9em 2em;
  }
  .col-md-12 {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  h1 {
    color: #6c757d;
    font-weight: bold;
  }
  h3 {
    color: #6c757d;
    padding-bottom: 0.5em;
  }
  p {
    font-size: 1.2em;
    padding: 0.5em 0em;
  }
  img {
    width: 95%;
    height: 95%;
    object-fit: scale-down;
  }
  @media only screen and (max-width: 767px) {
    min-height: 45vh;
    .row {
      padding: 5em 1em 2em 1em;
    }
    h1 {
      font-size: 1.2em;
      padding-bottom: 1.5em;
      align-items: center;
    }
    h3 {
      font-size: 1.2em;
      padding: 1em 0em;
    }
    p {
      font-size: 1em;
      padding: 0.3em 0em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 40vh;
    .row {
      padding: 6em 1em 3em 1em;
    }
    .col-md-12 {
      align-items: left;
    }
    h1 {
      font-size: 2.3em;
      padding-bottom: 1em;
    }
    h3 {
      font-size: 1.5em;
      padding: 1em 0em;
    }
    p {
      font-size: 1.4em;
      padding: 0.3em 0em;
    }
  }
`;
export default BlogSection3;
