import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Button } from "react-bootstrap";
import Blog1 from "../../img/blog1.jpg";

const BlogSection1 = () => {
  return (
    <BlogS1>
      <Container>
        <Row>
          <Col md={12}>
            <h1>How to Improve Teamwork in the Workplace</h1>
            <div>
              <img src={Blog1} alt="blog1image" />
            </div>
            <p>
              Teamwork makes the dream work. It's a decent saying, but the
              advice is incomplete. When it comes to improving teamwork,
              defining what improvement looks like is the first step. Improving
              teamwork is less about doing that literally than it is about
              establishing the outcome you're trying to improve. One cannot
              simply "improve teamwork" for the sake of improvement alone.
              Instead, you and your team are attempting to accomplish something
              that has yet to be done -- and defining that clearly and often is
              paramount.
            </p>
            <p>
              Are you trying to achieve your sales goals, drive greater employee
              performance or create technology to better serve your customer
              base?
            </p>
            <p>
              Once you've established the "what," you can focus on working best
              as a team to achieve those goals . This comes from setting your
              goal(s), understanding how your team collaborates most efficiently
              and placing individuals in the roles that best fit them, for the
              success of that individual and of the team.
            </p>

            <h3>Why Is Teamwork Important in the Workplace?</h3>
            <p>
              If retaining your best people, driving revenue and engaging your
              employees are important to your business model, teamwork should be
              important to you, as well.
            </p>
            <p>
              Two main ideas must be understood to appreciate the importance of
              teams in the workplace.
            </p>
            <p>
              1) Individuals who know their strengths work together to form
              better partnerships, and more thoughtful partnerships create
              stronger teams. Strong teams start with the individual.
            </p>
            <p>
              2) The strengths and dynamics of your team directly affect
              business outcomes.
            </p>
            <p>
              Consider your business strictly in terms of its end goal. Are you
              trying to sell a product, increase donations or provide a service?
              For a moment, don't think about relationships. As you think about
              your goal, you should consider all business components that
              directly affect it. Now reintroduce the relationships -- your
              people matter, and the way your teams work together matters to the
              business goals of your organization.
            </p>
            <h4>Strong teams start with the individual !</h4>
          </Col>
        </Row>
      </Container>
    </BlogS1>
  );
};

const BlogS1 = styled.div`
  min-height: 70vh;
  .row {
    padding: 9em 2em;
  }
  .col-md-12 {
    display: flex;
    flex-direction: column;
  }
  h1 {
    color: #6c757d;
    font-weight: bold;
    align-items: left;
  }
  h3 {
    color: #6c757d;
    padding: 0.5em 0em;
    font-weight: bold;
  }
  h4 {
    color: #6c757d;
    padding: 0.5em 0em;
    font-weight: bold;
    align-items: left;
  }
  p {
    font-size: 1.2em;
    padding: 0.5em 0em;
  }
  img {
    width: 90%;
    height: 90%;
    object-fit: scale-down;
  }
  @media only screen and (max-width: 767px) {
    min-height: 45vh;
    .row {
      padding: 6em 1em 2em 1em;
    }
    h1 {
      font-size: 1.2em;
      padding-bottom: 0em;
      align-items: center;
    }
    h3,
    h4 {
      font-size: 1.2em;
    }
    p {
      font-size: 1em;
      padding: 0.3em 0em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 40vh;
    .row {
      padding: 7em 1em 3em 1em;
    }
    .col-md-12 {
      align-items: left;
    }
    h1 {
      font-size: 2em;
      padding-bottom: 0.1em;
    }
    h3,
    h4 {
      font-size: 1.7em;
      padding: 1em 0em;
    }
    p {
      font-size: 1.4em;
      padding: 0.3em 0em;
    }
  }
`;
export default BlogSection1;
