import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Button } from "react-bootstrap";
import Blog5 from "../../img/blog5.png";

const BlogSection5 = () => {
  return (
    <BlogS2>
      <Container>
        <Row>
          <Col md={12}>
            <h1>Good Code vs Bad Code</h1>
            <div>
              <img src={Blog5} alt="blog5image" />
            </div>
            <h3>Why writing good code matters, and how to do it</h3>
            <p>
              When writing code in any language, there are good coding practices
              — and there are really bad ones.
            </p>
            <p>
              Both may be correct as far as compiling and when they are run. But
              bad code can present some problems in development, debugging, and
              modifying. In the workplace, no matter how well your program runs,
              someone will have to read or alter your code at some point.
            </p>
            <p>
              They may have to add new features, correct a rare bug, or they
              might just want to read it to understand how it works. Similarly,
              you will have to read someone else’s code to do the same thing.
              Everyone will get along a whole lot better if the code is readable
              and understandable.
            </p>
            <p>
              To know the importance of good quality code, let’s try to
              understand what a bad quality of code can lead to. Badly written
              code could lead to financial losses or waste of time needed for
              further maintenance, enhancements, or adjustments to software.
            </p>
            <p>
              You write your code once, but then follow it numerous times after.
              Hence, documenting your code becomes really important, and naming
              conventions become really important.
            </p>
            <p>
              Many times, some people they do not remember which code or logic
              they wrote a few days ago. Now, couple it with writing bad code
              style, and you will take more time to understand what you did.
            </p>
            <p>
              Things start to go haywire when an artist fails to understand
              their own piece of art!
            </p>
            <h3>Key Points to Keep in Mind While Writing Code</h3>
            <h5>1) Comments to the rescue</h5>
            <p>
              Most modern languages have declarative documented comments, and
              their use, in conjunction with single and multi-line comments,
              makes code more understandable — and, hence, maintainable.
            </p>
            <p>
              Good code comments explain why things are done, not what is done.
              The code itself explains what is done. The need for comments
              should be minimal.
            </p>
            <h5>2) Indentation</h5>
            <p>
              Good code is properly structured, as shown in the figure. It
              should be obvious for the person trying to understand code where a
              block of code starts and where it ends, so that following the
              logic of the code base becomes evident and straightforward.
            </p>
            <h5>3) README’s</h5>
            <p>
              It is annoying when you have a project’s codebase in front of you,
              but set up and first execution takes hours to get you rolling.
              This is where README’s come in handy.
            </p>
            <p>
              It is always better to have a brief intro to a project before you
              access the code, and a properly structured README does exactly
              that. A properly structured README looks like the below:
            </p>
            <h5>4) Naming conventions</h5>
            <p>
              Many times, we come across a Class with the name Apimanager, but
              looking at the name, the purpose of class is not clear.
            </p>
            <p>
              According to best coding practice norms, our classes should follow
              the single responsibility principle (SRP). Proper naming
              conventions, coupled with SRP, make our lives much easier while
              trying to follow a project.
            </p>
            <p>
              Naming conventions should also vary for different scopes if a
              Class is performing an intensive unit of work. This is to
              differentiate when and where a variable becomes out of scope just
              by looking at the block of code.
            </p>
            <p>
              Use meaningful naming conventions for all but the most transient
              of objects. The name of something is informative about when and
              how to use the object.
            </p>
          </Col>
        </Row>
      </Container>
    </BlogS2>
  );
};

const BlogS2 = styled.div`
  min-height: 70vh;
  .row {
    padding: 9em 2em;
  }
  .col-md-12 {
    display: flex;
    flex-direction: column;
  }
  h1 {
    color: #6c757d;
    font-weight: bold;
  }
  h3 {
    color: #6c757d;
    padding: 1em 0em 0.3em 0em;
    margin-bottom: 1em;
    font-weight: bold;
    border-bottom: 2px solid;
    border-color: #6c757d;
  }
  h5 {
    color: #6c757d;
    padding: 0.5em 0em;
    font-weight: bold;
  }
  p {
    font-size: 1.2em;
    padding-top: 0.2em;
  }
  img {
    width: 90%;
    height: 90%;
    object-fit: scale-down;
  }
  @media only screen and (max-width: 767px) {
    min-height: 30vh;
    .row {
      padding: 5em 1em 2em 1em;
    }
    .col-md-12 {
    }
    h1 {
      font-size: 1.2em;
      padding-bottom: 0.1em;
      align-items: center;
    }
    h3 {
      font-size: 1.2em;
      padding: 0em 0em 0.3em 0em;
    }
    p {
      font-size: 1em;
      padding: 0.3em 0em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 40vh;
    .row {
      padding: 6em 1em 3em 1em;
    }
    .col-md-12 {
      align-items: left;
    }
    h1 {
      font-size: 2.3em;
      padding-bottom: 0.1em;
    }
    h3 {
      font-size: 1.5em;
      padding: 0em 0em 0.3em 0em;
    }
    p {
      font-size: 1.4em;
      padding: 0.3em 0em;
    }
  }
`;
export default BlogSection5;
