import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Button } from "react-bootstrap";
import Blog2 from "../../img/blog2.png";

const BlogSection2 = () => {
  return (
    <BlogS2>
      <Container>
        <Row>
          <Col md={12}>
            <h1>
              Frontend Development Outsourcing Challenges And How To Overcome
              Them
            </h1>
            <div>
              <img src={Blog2} alt="blog2image" />
            </div>
            <p>
              The continued growth of the web is depleting local talent pools,
              making more and more businesses turn to alternative approaches to
              hiring talent - remote workers and outsourcing. Can those models
              work for front-end development?
            </p>
            <p>
              First of all, let’s explain the confusion between what is a remote
              team and what is an outsourced team. If you have a remote team it
              means you hire people that work for you and only you but are not
              located in the main office. They are a part of your team despite
              not being physically present.Let’s now dive into the most commonly
              mentioned problems with outsourcing:
            </p>
            <h3>Lack of quality</h3>
            <p>
              This accusation is strictly connected to errors during the process
              of vendor selecting. Many companies decide to outsource while
              being on a rather tight budget and time frame. They make their
              decision without in-depth research and go for the cheapest option.
            </p>
            <p>
              Insufficient analysis of potential outsourcing providers can lead
              to contracting an inexperienced team, which not only will leave
              you with a bad quality product but also the need to spend twice
              the money you initially planned on another vendor that will fix
              their mistakes.
            </p>
            <p>
              If you are not sure how to properly choose an outsourcing partner,
              our articles about How To Choose A Custom Software Development
              Company and 10 Questions You Should Ask A Software Outsourcing
              Company should help you out.
            </p>
            <h3>Changes in the team</h3>
            <p>
              When going through comments and internet forums I have noticed
              that many product owners complain that outsourced teams frequently
              introduce changes in their structures. And yes, developers working
              on your project may be replaced with new team members
              mid-development however, reputable providers will always give you
              somebody with a similar skill level.
            </p>
            <p>
              The new developer will be able to handle the tasks of his/her
              successor and hopefully introduce a breath of fresh air and
              possibly new solutions to the project. If you are truly against
              such actions, a well-formed contract will ensure the team stays
              with you from the beginning to the end of the project.
            </p>
            <h3>Lack of control</h3>
            <p>
              Apart from project failure, losing control over the development is
              what is feared the most.
            </p>
            <p>
              Yes, you are giving away team management since you are hiring a
              contractor and not an employee. However, you can always provide
              directions on how the process should be handled. It all comes to
              good negotiation and finding common ground.
            </p>
            <p>
              Giving up the control, or at least some of it, can also do you
              good. You gain more time to focus on the business-related
              operations, think about the launch, care for customers, not to
              mention more time for yourself.
            </p>
            <h3>Higher costs</h3>
            <p>
              The alleged higher costs of outsourced development are yet another
              obstacle that arises due to the lack of preparation from the
              business owner. People often underestimate the costs of
              development, not taking into account every possible scenario,
              development standstills or other unforeseen circumstances.
            </p>
            <p>
              To avoid the surprise it is best to clearly define your
              requirements and estimate time frames. This way you will be
              prepared to what to expect cost-wise and be sure that the timing
              is right.
            </p>
            <h3>Security issues</h3>
            <p>
              Obviously, giving somebody else access to sensitive data can be
              scary, but in reality, there is nothing to worry about as long as
              you go with a reliable vendor. Experienced outsourcing companies
              take extreme safety measures in order to provide their clients
              with the best service and ensure their information is protected.
            </p>
            <p>
              Security is something that should be deeply discussed during
              negotiations and should be one of the key points in the contract.
            </p>
            <h3>Summary</h3>
            <p>
              Every obstacle with outsourcing front-end development can be
              overcome with the right research and extensive preparations.
              Hiring a reliable contractor will ensure that you won't become the
              main character of the outsourcing horror stories.
            </p>
            <p>
              If you are still not 100% sure if outsourcing will bring you
              success, you can always consider trying out a mixed-team model:
              having both an in-house and outsourced team. This way, you will
              get the best of both worlds and will be able to test the
              outsourcing waters.
            </p>
            <p>
              And if you want to try outsourcing, make sure to check our amazing
              team of expert engineers ready to take on your next project.
            </p>
          </Col>
        </Row>
      </Container>
    </BlogS2>
  );
};

const BlogS2 = styled.div`
  min-height: 70vh;
  .row {
    padding: 9em 2em;
  }
  .col-md-12 {
    display: flex;
    flex-direction: column;
  }
  h1 {
    color: #6c757d;
    font-weight: bold;
  }
  h3 {
    color: #6c757d;
    padding: 0.9em 0em 0.4em 0em;
    font-weight: bold;
    border-bottom: 1px solid;
    border-color: #6c757d;
  }
  p {
    font-size: 1.2em;
    padding: 0.3em 0em;
  }
  img {
    width: 90%;
    height: 90%;
    object-fit: scale-down;
  }
  @media only screen and (max-width: 767px) {
    min-height: 45vh;
    .row {
      padding: 5em 1em 2em 1em;
    }
    h1 {
      font-size: 1.2em;
      padding-bottom: 0.1em;
      align-items: center;
    }
    h3 {
      font-size: 1.2em;
      padding: 1em 0em;
    }
    p {
      font-size: 1em;
      padding: 0.3em 0em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 40vh;
    .row {
      padding: 6em 1em 3em 1em;
    }
    .col-md-12 {
      align-items: left;
    }
    h1 {
      font-size: 2.3em;
      padding-bottom: 1em;
    }
    h3 {
      font-size: 1.5em;
      padding: 1em 0em;
    }
    p {
      font-size: 1.4em;
      padding: 0.5em 0em;
    }
  }
`;
export default BlogSection2;
