import { createGlobalStyle } from "styled-components";

const GlobalStyle = createGlobalStyle`
*{
    box-sizing:border-box;
    margin: 0px;
    padding: 0px;
}
body,html {
    background:#ffffff;
    font-family: 'Lato', sans-serif;
    overflow-x:hidden;
}



`;
export default GlobalStyle;
