import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Button } from "react-bootstrap";

const CareersSection1 = () => {
  return (
    <CareerS1>
      <Container>
        <Row id="first">
          <Col>
            <h1>Careers</h1>
            <h3>Join the team , be a part of our story!</h3>
            <p>
              We are always looking for talented and enthusiastic people to join
              the team. If you have a passion for Information Technology, then
              send us your updated CV to ovitlabcompany@gmail.com.
            </p>
            <Button variant="primary" size="lg">
              Open Vacancies
            </Button>
          </Col>
        </Row>
      </Container>
    </CareerS1>
  );
};

const CareerS1 = styled.div`
  min-height: 50vh;
  #first {
    padding: 12em 0em 3em 0em;
    h1 {
      padding: 0.3em 0em;
      font-size: 3.5em;
      font-weight: bold;
      color: #6c757d;
    }
    h3 {
      padding: 0.5em 0em;
      font-size: 2em;
      font-weight: bold;
      color: #6c757d;
    }
    p {
      padding: 0.3em 0em 1.5em 0em;
      font-size: 1.5em;
      color: #6c757d;
      display: block;
    }
  }

  @media only screen and (max-width: 767px) {
    min-height: 30vh;

    #first {
      padding-top: 6em;
      padding-left: 1em;
      h3 {
        padding-bottom: 0.2em;
      }
      h1 {
        padding: 0.1em 0em;
        font-size: 2.2em;
      }
      p {
        font-size: 1em;
        padding-bottom: 1em;
      }
      Button {
        font-weight: 400;
        font-size: 0.9em;
      }
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 25vh;

    #first {
      padding-top: 8em;
      h1 {
        padding: 0.3em 0em;
      }
      h5 {
        padding: 0em;
      }
    }
  }
`;
export default CareersSection1;
