import React from "react";
import styled from "styled-components";
import About1 from "../../img/home1.jpg";
import { useHistory } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container, Row, Card, Col } from "react-bootstrap";

const AboutUsSection2 = () => {
  return (
    <AboutUsS2>
      <Container>
        <Row>
          <Col sm>
            <Card>
              <Card.Body>
                <Card.Subtitle className="mb-2 text-muted">
                  Vision
                </Card.Subtitle>
                <Card.Text>
                  We provide digital solutions to enhance the business of our
                  customers,whilst creating better technologies that benifit the
                  customer.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col sm>
            <Card>
              <Card.Body>
                <Card.Subtitle className="mb-2 text-muted">
                  Mission
                </Card.Subtitle>
                <Card.Text>
                  We will be the most valued business partner of all our
                  customers.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col sm>
            <Card>
              <Card.Body>
                <Card.Subtitle className="mb-2 text-muted">
                  Values
                </Card.Subtitle>
                <Card.Text>
                  <span>Energy -</span> Capture opportunities and make things
                  happen.
                </Card.Text>
                <Card.Text>
                  <span> Excellence -</span> Do things better than anyone else
                  in our industry.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </AboutUsS2>
  );
};

const AboutUsS2 = styled.div`
  min-height: 60vh;
  background-color: #eeecf5;
  padding: 6em 4em 3em 4em;
  .row {
    padding: 3em 0em 2em 0em;
  }
  .col-sm {
    padding-bottom: 2em;
  }
  .card {
    border-radius: 0;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    padding: 2em;
    font-size: 1em;
    height: 100%;
    width: 100%;
    border-radius: 0em;
  }
  .card-subtitle {
    margin: 0;
    font-size: 1.7em;
    font-weight: bold;
    padding-bottom: 0.3em;
  }
  p {
    span {
      font-weight: bold;
    }
  }
  @media only screen and (max-width: 767px) {
    min-height: 50vh;
    padding: 0.5em;
    .card {
      padding-bottom: 1em;
      margin: 0.3em;
      height: 100%;
      width: 100%;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 40vh;
    padding: 2em 1em;
    .row {
      padding: 5em 0em 1em 0em;
    }
  }
`;
export default AboutUsSection2;
