import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Button } from "react-bootstrap";
import Career3 from "../../img/career3.png";

const CareersSection3 = () => {
  return (
    <CareerS3>
      <Container>
        <Row>
          <img src={Career3} alt="career3" />
        </Row>
      </Container>
    </CareerS3>
  );
};
const CareerS3 = styled.div`
  min-height: 30vh;
  padding: 1em 0em;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  @media only screen and (max-width: 767px) {
    min-height: 22vh;
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 28vh;
  }
`;
export default CareersSection3;
