import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Card } from "react-bootstrap";
import { FaBuffer, FaBong, FaRegClock, FaThumbsUp } from "react-icons/fa";

const ServiceSection1 = () => {
  return (
    <ServiceS1>
      <Container>
        <Row id="first">
          <Col>
            <h1>Services</h1>
            <h3>What we do</h3>
            <p>
              Digital presence is a powerful tool to deliver business value and
              upgrade performance. The team at OvitLab masterfully combines
              proven development techniques and innovations to provide an
              exceptional user experience.
            </p>
          </Col>
        </Row>
        <Row id="second">
          <Col md={3}>
            <Card>
              <FaBuffer />
              <Card.Body>
                <Card.Title>Top-notch front-end</Card.Title>
                <Card.Text>
                  A user-centric interface will help your business stand out
                  among the competition.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col md={3}>
            <Card>
              <FaBong />
              <Card.Body>
                <Card.Title>Reliable back-end</Card.Title>
                <Card.Text>
                  An easy-to-maintain and crash-free back-end increases customer
                  interaction.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col md={3}>
            <Card>
              <FaRegClock />
              <Card.Body>
                <Card.Title>Full-cycle delivery</Card.Title>
                <Card.Text>
                  Our team will cover the project lifecycle from first draft to
                  market-ready brand.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
          <Col md={3}>
            <Card>
              <FaThumbsUp />
              <Card.Body>
                <Card.Title>Skilled professionals</Card.Title>
                <Card.Text>
                  Excellence in execution provided by full-time professionals.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </ServiceS1>
  );
};
const ServiceS1 = styled.div`
  min-height: 70vh;
  padding-bottom: 7em;
  #first {
    padding: 8em 0em 2em 0em;

    h1 {
      padding: 0.6em 0em;
      font-size: 3.5em;
      font-weight: bold;
      color: #6c757d;
    }
    h3 {
      padding: 0.3em 0em;
      font-size: 2em;
      font-weight: bold;
      color: #6c757d;
    }
    p {
      padding: 0.3em 0em;
      font-size: 1em;
    }
  }
  #second {
    padding: 2em 2em 1em 0em;
    .card {
      width: 100%;
      height: 100%;
      padding: 1em 0.1em 0.2em 0.1em;
      align-items: center;
      border-radius: 0;
      box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    }
    .card-title {
      align-items: left;
      color: #6c757d;
      font-weight: bold;
      margin-bottom: 1.5em;
    }
    svg {
      color: #4a90e2;
      font-size: 5em;
    }
  }
  @media only screen and (max-width: 767px) {
    min-height: 30vh;
    padding-bottom: 2em;
    #first {
      padding: 6em 0em 0em 0em;
      h3 {
        padding-bottom: 0.2em;
        font-size: 1.6em;
      }
      h1 {
        padding: 0.3em 0em;
        font-size: 2.4em;
      }
    }
    #second {
      padding: 0;
      margin: 0em 0.5em;
      .col-md-3 {
        padding: 0.3em;
      }
      .card-title {
        margin-bottom: 0.7em;
      }
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 25vh;
    padding-bottom: 4.5em;
    #first {
      p {
        font-size: 1.4em;
        padding: 0.4em 0em 0em 0em;
      }
    }
  }
  #second {
    padding: 1em 0em;
  }
`;
export default ServiceSection1;
