import React from "react";
import styled from "styled-components";
import Career1 from "../../img/career1.png";
import { Container, Row, Col, Card } from "react-bootstrap";
import { FaQuoteLeft, FaQuoteRight } from "react-icons/fa";

const CareersSection2 = () => {
  return (
    <CareerS2>
      <Container>
        <Row>
          <Col>
            <h3>
              <FaQuoteLeft />
              Employees are a company’s greatest asset – they’re your
              competitive advantage. You want to attract and retain the best;
              provide them with encouragement, stimulus and make them feel that
              they are an integral part of the company’s mission.
              <FaQuoteRight />
            </h3>
          </Col>
        </Row>
      </Container>
    </CareerS2>
  );
};
const CareerS2 = styled.div`
  min-height: 50vh;
  padding-top: 2em;
  background-color: #ffffff;
  .row {
    padding: 3em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .col {
    padding: 3em;
  }
  h3 {
    color: #6c757d;
    line-height: 1.5em;
  }
  svg {
    font-size: 1.5em;
    color: #00488f;
    vertical-align: top;
    padding: 0em 0.2em 0.3em 0em;
  }
  @media only screen and (max-width: 767px) {
    min-height: 25vh;
    .row {
      padding: 0em 1em 1em 1em;
    }
    .col {
      padding: 1em;
    }
    h3 {
      font-size: 1.2em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 20vh;
    .row {
      padding: 0em 1em 2em 1em;
    }
    .col {
      padding: 1em;
    }
    h3 {
      font-size: 1.5em;
    }
  }
`;
export default CareersSection2;
