import React from "react";
import styled from "styled-components";
import Blog1 from "../../img/blog1.jpg";
import Blog2 from "../../img/blog2.png";
import Blog3 from "../../img/blog3.jpg";
import Blog4 from "../../img/blog4.jpg";
import Blog5 from "../../img/blog5.png";
import Blog6 from "../../img/blog6.jpg";
import { Container, Row, Col, Card, Button } from "react-bootstrap";
import { useHistory } from "react-router-dom";

const BlogSectionMain = () => {
  const history = useHistory();
  const navigate1 = () => history.push("/blog1");
  const navigate2 = () => history.push("/blog2");
  const navigate3 = () => history.push("/blog3");
  const navigate4 = () => history.push("/blog4");
  const navigate5 = () => history.push("/blog5");
  const navigate6 = () => history.push("/blog6");
  return (
    <BlogSMain>
      <Container>
        <Row id="first">
          <Col>
            <h1> Blog - Ovit Lab Insights</h1>
            <p>
              Our inspiring blog about digital product strategy, user
              experience, design, engineering, and innovation apart from code.
            </p>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <Card>
              <Card.Body>
                <Card.Title>
                  <span>How to Improve Teamwork in the Workplace</span>
                </Card.Title>
                <Card.Text>
                  Teamwork makes the dream work. It's a decent saying, but the
                  advice is incomplete.Teamwork cannot exist apart from the
                  "something" that your team is working to accomplish.
                </Card.Text>
                <Button variant="primary" onClick={navigate1}>
                  Read More
                </Button>
              </Card.Body>
              <div>
                <img src={Blog1} alt="blog1image" />
              </div>
            </Card>
          </Col>
          <Col md={6}>
            <Card>
              <Card.Body>
                <Card.Title>
                  Frontend Development Outsourcing Challenges
                </Card.Title>
                <Card.Text>
                  The continued growth of the web is depleting local talent
                  pools, making more and more businesses turn to alternative
                  approaches to hiring talent - remote workers and outsourcing.
                </Card.Text>
                <Button variant="primary" onClick={navigate2}>
                  Read More
                </Button>
              </Card.Body>
              <div>
                <img src={Blog2} alt="blog2image" />
              </div>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <Card>
              <Card.Body>
                <Card.Title>
                  How to choose a Custom Software Development Company
                </Card.Title>
                <Card.Text>
                  Choosing the right software development partner is the key
                  factor in successful outsourcing.
                </Card.Text>
                <Button variant="primary" onClick={navigate3}>
                  Read More
                </Button>
              </Card.Body>
              <div>
                <img src={Blog3} alt="blog3image" />
              </div>
            </Card>
          </Col>
          <Col md={6}>
            <Card>
              <Card.Body>
                <Card.Title>
                  We live in the mobile era where people more than ever before
                  are using mobile devices
                </Card.Title>
                <Card.Text>
                  On the streets, in restaurants, on a bus, waiting in line,
                  Everywhere you look people are using mobile phones.
                </Card.Text>
                <Button variant="primary" onClick={navigate4}>
                  Read More
                </Button>
              </Card.Body>
              <div>
                <img src={Blog4} alt="blog4image" />
              </div>
            </Card>
          </Col>
        </Row>
        <Row>
          <Col md={6}>
            <Card>
              <Card.Body>
                <Card.Title>Good Code vs Bad Code</Card.Title>
                <Card.Text>
                  When writing code in any language, there are good coding
                  practices — and there are really bad ones.
                </Card.Text>
                <Button variant="primary" onClick={navigate5}>
                  Read More
                </Button>
              </Card.Body>
              <div>
                <img src={Blog5} alt="blog5image" />
              </div>
            </Card>
          </Col>
          <Col md={6}>
            <Card>
              <Card.Body>
                <Card.Title>
                  If Our Future Is Digital, How Will It Change the World?
                </Card.Title>
                <Card.Text>
                  By 2050 there will be 9 billion people to feed, clothe,
                  transport, employ and educate. We’re committed to a
                  growth-driven world economy that must inflate for centuries,
                </Card.Text>
                <Button variant="primary" onClick={navigate6}>
                  Read More
                </Button>
              </Card.Body>
              <div>
                <img src={Blog6} alt="blog6image" />
              </div>
            </Card>
          </Col>
        </Row>
      </Container>
    </BlogSMain>
  );
};

const BlogSMain = styled.div`
  min-height: 50vh;
  padding: 2em 0em 6em 0em;
  #first {
    padding: 7em 0em 2em 0em;
  }
  .row {
    padding: 0.2em 0em;
    display: flex;
    flex-direction: row;
  }
  .col-md-6 {
    padding: 0.8em;
    display: flex;
    flex-direction: column;
  }
  h1 {
    padding: 0.5em 0em;
    color: #6c757d;
    font-weight: bold;
  }
  p {
    font-size: 1.2em;
    display: block;
  }
  .card {
    padding: 0.3em;
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
  .card-title {
    font-size: 1.5em;
    color: #6c757d;
    font-weight: bold;
  }
  .card-text {
    font-size: 1.1em;
    color: black;
  }
  img {
    width: 100%;
    height: 100%;
    object-fit: scale-down;
    opacity: 0.8;
  }

  @media only screen and (max-width: 767px) {
    min-height: 40vh;
    padding: 1em 1em 3em 1em;
    #first {
      padding: 3em 0em 0em 0em;
    }
    .row {
      padding: 0.1em 0em;
    }
    .col-md-6 {
      padding: 0.3em;
    }
    h1 {
      font-size: 1.6em;
    }

    .card-title {
      font-size: 1em;
    }
    .card-text {
      font-size: 1em;
    }
    img {
      opacity: 0.95;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    h1 {
      font-size: 2.5em;
    }
    img {
      opacity: 0.95;
    }
  }
`;
export default BlogSectionMain;
