import React from "react";
import styled from "styled-components";
import "bootstrap/dist/css/bootstrap.min.css";
import { Badge, Card, CardDeck, Container, Row, Col } from "react-bootstrap";
import Brief from "../../img/brief.svg";
import Key from "../../img/key.svg";
import Team from "../../img/team.svg";

const HomeSection2 = () => {
  return (
    <HomeS2>
      <Container>
        <Row id="1">
          <Col sm>
            <h5>
              <Badge variant="primary">OUR ADVANTAGES</Badge>
            </h5>
          </Col>
        </Row>
        <Row id="2">
          <Col sm>
            <h1>Why Choose Us</h1>
          </Col>
        </Row>
        <Row id="main">
          <Col sm>
            <Card>
              <Card.Img variant="top" src={Brief} alt="brief" />
              <Card.Body>
                <Card.Title>Unique Approach</Card.Title>
                <Card.Text>
                  We seamlessly engage with your company as a part of your
                  staff.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>

          <Col sm>
            <Card>
              <Card.Img variant="top" src={Team} alt="team" />
              <Card.Body>
                <Card.Title>GreatTeam</Card.Title>
                <Card.Text>
                  Our team represents a mix of agency and enterprise experience.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>

          <Col sm id="last">
            <Card>
              <Card.Img variant="top" src={Key} alt="key" />
              <Card.Body>
                <Card.Title>B2B Experience</Card.Title>
                <Card.Text>
                  We have been serving premier B2B clients for many years.
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Container>
    </HomeS2>
  );
};

const HomeS2 = styled.div`
  background: #ffffff;
  min-height: 50vh;
  display: flex;
  padding-top: 1em;
  padding-bottom: 8em;
  h5 {
    display: flex;
    justify-content: center;
    padding-top: 3em;
  }
  h1 {
    display: flex;
    justify-content: center;
    padding-top: 0.4em;
    padding-bottom: 1em;
  }
  .card {
    background-color: #ffffff;
    border-radius: 0;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    align-items: center;
    padding: 2em 0em;
    width: 100%;
    height: 100%;
    img {
      width: 50%;
      height: 40%;
    }
  }
  .card-title {
    text-align: center;
    font-weight: bolder;
  }
  .card-text {
    text-align: center;
  }

  @media only screen and (max-width: 767px) {
    padding-bottom: 2em;
    padding-top: 1em;
    min-height: 30vh;
    h5 {
      margin: 0 auto;
      padding: 1em;
    }
    h1 {
      padding: 0em 0.3em 0.5em 0.4em;
      display: flex;
      font-size: 2em;
      flex-direction: row;
      align-items: center;
      margin: 0 auto;
    }
    .card {
      padding: 1.5em 0em;
      img {
        object-fit: scale-down;
      }
    }
    .card-text {
      padding: 0em;
    }

    .col-sm {
      padding-bottom: 0.7em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 50vh;
  }
`;

export default HomeSection2;
