import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Button } from "react-bootstrap";
import Blog6 from "../../img/blog6.jpg";

const BlogSection6 = () => {
  return (
    <BlogS6>
      <Container>
        <Row>
          <Col md={12}>
            <h1>If Our Future Is Digital, How Will It Change the World?</h1>
            <p>
              By 2050 there will be 9 billion people to feed, clothe, transport,
              employ and educate. We’re committed to a growth-driven world
              economy that must inflate for centuries, supplying limitless
              consumption to everyone. With new tech, could we add a digital
              world that helps everyone succeed and prosper while working
              together? Could we become a successful world where greatness is
              normal?
            </p>
            <p>
              One day greatness will be in our grasp. But rather than waiting,
              can we reach it now?
            </p>
            <p>
              Here’s the good news: digital devices have made your life better.
              Here’s the bad news: although incredibly cool, devices are still
              in early stages of development. Maybe that’s not really bad, when
              you think of what’s coming next.
            </p>
            <p>
              Until now, the devices you’ve had in your hands and on your desks
              have offered quite both usefulness and fun. But you couldn’t call
              it a fully digital world yet. It’s not even close.
            </p>
            <p>
              There are limitations. You switch on your gadgets and wait. Or you
              have to find the app you want and wait for it. They might have an
              operating system whose principles you have to follow. They allow
              you a little creativity, but only a little.
            </p>
            <p>
              Then your apps or data might not work with another device that you
              bought, say, last week, or last year. The last time you saw a
              friend with a new device you don’t have, could theirs be better?
              Maybe!
            </p>
            <p>Clearly, there is room to dream about new technology.</p>
            <p>
              There’s room to dream about building the world we want, instead of
              the one we’re turning into.
            </p>
            <div>
              <img src={Blog6} alt="blog6image" />
            </div>
            <h3>Can a New Digital Window Display a New Future?</h3>
            <p>
              This new option started in 2007 with big questions: Can we
              envision a world where tech helps everyone succeed and prosper?
              Can that world be designed and built now, without waiting for “the
              future” to arrive?
            </p>
            <p>
              The Expandiverse grew steadily through years of private and
              confidential tech and IP (Intellectual Property) development.
            </p>
            <p>
              Our world is full of screens. We keep them in our hands, purses
              and pockets, next to our beds while we sleep, and surround
              ourselves with screens on our desks and counter tops. Our TV sets
              are morphing into interactive screens as we put them online so
              they display everything for free.
            </p>
            <p>
              What if all our screens, everywhere, were a two-way networked
              system that turns the Earth into a digital room with everyone in
              it? What if that networked system brought everyone the world’s
              best services, resources and knowledge based on what we do, as a
              normal part of everyday life?
            </p>
            <p>
              Billions of us. Together. All of us succeeding as much as we
              choose. All the time.
            </p>
            <p>
              Let’s dream a little. Let’s dream about technology we could build,
              about a world we could enjoy.
            </p>
          </Col>
        </Row>
      </Container>
    </BlogS6>
  );
};

const BlogS6 = styled.div`
  min-height: 70vh;
  .row {
    padding: 9em 2em;
  }
  .col-md-12 {
    display: flex;
    flex-direction: column;
  }
  h1 {
    color: #6c757d;
    font-weight: bold;
    padding-bottom: 1em;
  }
  h3 {
    color: #6c757d;
    padding: 0.7em 0em;
    font-weight: bold;
    font-size: 2em;
  }
  p {
    font-size: 1.2em;
    padding-top: 0.5em;
  }
  img {
    width: 80%;
    height: 80;
    object-fit: scale-down;
  }
  @media only screen and (max-width: 767px) {
    min-height: 45vh;
    .row {
      padding: 5em 1em 2em 1em;
    }
    h1 {
      font-size: 1.2em;
      padding-bottom: 0.1em;
      align-items: center;
    }
    h3 {
      font-size: 1.2em;
      padding: 1em 0em;
    }
    p {
      font-size: 1em;
      padding: 0.3em 0em;
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 40vh;
    .row {
      padding: 6em 1em 3em 1em;
    }
    .col-md-12 {
      align-items: left;
    }
    h1 {
      font-size: 2.3em;
      padding-bottom: 0.1em;
    }
    h3 {
      font-size: 1.5em;
      padding: 1em 0em;
    }
    p {
      font-size: 1.4em;
      padding: 0.3em 0em;
    }
  }
`;
export default BlogSection6;
