import React from "react";
import styled from "styled-components";
import { Container, Row, Col, Button } from "react-bootstrap";
import Blog4 from "../../img/blog4.jpg";
import Blog4a from "../../img/blog4a.png";

const BlogSection4 = () => {
  return (
    <BlogS4>
      <Container>
        <Row id="first">
          <Col md={12}>
            <h1>
              We live in the mobile era where people more than ever before are
              using mobile devices.
            </h1>
            <div>
              <img src={Blog4} alt="blog4image" />
            </div>
            <p>
              On the streets, in restaurants, on a bus, waiting in line,
              Everywhere you look people are using mobile phones. From kids at a
              rather young age to middle-aged businessmen and elders - we seem
              glued to our smartphones from sunrise to sunset. The development
              of a mobile phone that evolved into a smartphone changed the way
              we communicate, shop, browse the web, take pictures etc. It has
              revolutionized our world and changed it forever. Companies
              recognize the importance of being mobile-friendly. More and more
              websites are being adapted to smaller screens or getting dedicated
              native applications. Let’s face the facts, your business cannot
              operate well if your website sucks, no matter what kind of
              business you run. This is why a mobile-first approach to website
              development was born and it is not going away.
            </p>
          </Col>
        </Row>
        <Row id="second">
          <Col md={12}>
            <h3>What is a mobile-first design?</h3>
            <div>
              <img src={Blog4a} alt="blog4image" />
            </div>
            <p>
              Some time ago, websites were desktop-oriented. Features,
              functions, special graphics, audio and visual effects were adapted
              to bigger screens, providing a great user experience, but also
              requiring faster internet connection. ‘Traditional’ industries are
              still focused on desktop but this is not a good way of thinking in
              2021.
            </p>
            <p>
              As a response to the popularity of mobile phones with built-in
              browsers rising, a notion of mobile-friendly web design was
              created. It means that it will render on a smaller screen, but we
              should not expect it to look that great. Those websites opened on
              a mobile phone look exactly the way they look on a bigger screen.
              This means zooming in and out to navigate between pages is a must
              - a user experience killer.
            </p>
            <p>
              Then the responsive web design came to life. This is a process of
              creating websites that work on any mobile device with any screen
              size without the necessity to zoom in and out to see the entire
              content. This way, the content is preserved and user experience is
              saved.
            </p>
            <p>
              But things went a step further when the mobile-first design was
              born. But, what exactly is the mobile-first design and why is it
              so important? Well, it is all quite simple. Mobile-first design is
              literally sketching, designing and developing websites for
              mobile-first and later on, scaling them up to larger screens.Among
              the advantages of a mobile-first approach is improved Google
              Ranking - since in 2018 Google switched to mobile-first indexing
              it ranks the mobile version of your website before the desktop one
              as well as increased mobile conversions - the better the mobile
              website the more probable customers will come back.
            </p>
          </Col>
        </Row>
      </Container>
    </BlogS4>
  );
};

const BlogS4 = styled.div`
  min-height: 60vh;
  #first {
    padding: 9em 2em 1em 2em;
    .col-md-12 {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    h1 {
      color: #6c757d;
      font-weight: bold;
      padding-bottom: 0.3em;
    }

    p {
      font-size: 1.2em;
      padding: 0.9em 0em 0.1em 0em;
    }
    img {
      width: 95%;
      height: 95%;
      object-fit: scale-down;
    }
  }
  #second {
    padding: 2em 2em 3em 2em;
    .col-md-12 {
      display: flex;
      flex-direction: column;
    }
    h3 {
      align-items: left;
    }
    img {
      width: 90%;
      height: 90%;
      object-fit: scale-down;
    }
    p {
      font-size: 1.2em;
      padding: 0.5em 0em;
    }
    h3 {
      color: #6c757d;
      padding-bottom: 0.7em;
      font-weight: bold;
      font-size: 2em;
    }
  }

  @media only screen and (max-width: 767px) {
    min-height: 45vh;
    #first {
      padding: 5em 1em 0em 1em;
      h1 {
        font-size: 1.2em;
        padding-bottom: 1.5em;
        align-items: center;
      }

      p {
        font-size: 1em;
        padding: 0.3em 0em;
      }
    }
    #second {
      padding: 1.1em;

      h3 {
        font-size: 1.2em;
        padding-bottom: 1em;
        align-items: left;
      }
      p {
        font-size: 1em;
        padding: 0.3em 0em;
      }
    }
  }

  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 40vh;
    .first {
      padding: 9em 1em 3em 1em;
      .col-md-12 {
        align-items: left;
      }
      h1 {
        font-size: 2em;
        padding-bottom: 1em;
      }

      p {
        font-size: 1.4em;
        padding: 0.3em 0em;
      }
    }
    #second {
      padding: 2.2em;

      h3 {
        font-size: 1.5em;
        padding-bottom: 1em;
      }
      p {
        font-size: 1.4em;
        padding: 0.3em 0em;
      }
    }
  }
`;
export default BlogSection4;
