import React from "react";
import styled from "styled-components";
import Home3 from "../../img/home3.png";
import "bootstrap/dist/css/bootstrap.min.css";
import { Figure, Container, Row, Col, Card } from "react-bootstrap";

const HomeSection3 = () => {
  return (
    <HomeS3>
      <Container>
        <Row>
          <Col xs={{ span: 12, order: 2 }} md={{ span: 6, order: 1 }}>
            <img src={Home3} alt="home3" />
          </Col>

          <Col
            xs={{ span: 12, order: 1 }}
            md={{ span: 6, order: 2 }}
            id="description"
          >
            <h2>We Believe in Ideas That Create Change</h2>
            <p id="main">
              We don't just start developing. As solution partners we research,
              analyze, plan, document your project requirements and then execute
              it to ensure Results.
            </p>
            <h5>
              "Our company helps brands worldwide to better understand what
              their clients need and to see the full picture of how
              communications work."
            </h5>
          </Col>
        </Row>
      </Container>
    </HomeS3>
  );
};

const HomeS3 = styled.div`
  background: #f5f5f5;
  min-height: 60vh;
  padding: 4em;

  img {
    width: 100%;
    height: 100%;
  }
  .col-md-6 {
    align-items: center;
    padding-top: 3em;
  }
  #description {
    padding: 3em 0em 1em 1em;
    h2 {
      padding-bottom: 0.5em;
      color: #000000;
    }
    h5 {
      padding: 0.8em 0em;
      font-weight: 300;
      color: #787878;
    }
  }
  @media only screen and (max-width: 767px) {
    padding: 2em;
    min-height: 33vh;
    .col-md-6 {
      padding-top: 2em;
    }
    #description {
      padding: 2em 1em 0.5em 1em;
      h2 {
        padding-bottom: 0.2em;
        font-size: 1.5em;
      }
      h5 {
        padding: 0.1em 0em;
      }
    }
  }
  @media only screen and (min-width: 768px) and (max-width: 1024px) {
    min-height: 35vh;
    .col-md-6 {
      padding-top: 1em;
    }
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      object-position: center;
    }
    #main {
      font-size: 1.3em;
    }
  }
`;

export default HomeSection3;
